body {
  font: menu;
  font-size: 100%;
  margin: 5rem auto;
  max-width: 720px;
  line-height: 1.7;
  background-color: whitesmoke;
}

.hovercard {
  border-bottom: 1px solid;
  color: #69e;
  cursor: help;
}
.hovercard-element {
  box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.2) !important;
  background-color: #fff !important;
}
.hovercard-arrow {
  border-bottom: 10px solid #fff !important;
}

pre {
  background-color: rgba(0, 0, 0, 0.1);
  padding: 1rem 1.25rem;
}

.hovercard-element .hovercard-description::after {
  background: linear-gradient(transparent, #fff) !important;
}
.hovercard {
  white-space: nowrap;
}

.hovercard-element {
  background-color: whitesmoke;
  border-radius: 5px;
  position: absolute;
  width: 300px;
  padding: 15px 20px;
  z-index: 15;
  display: none;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  max-height: 200px;
  overflow: hidden;
}
.hovercard-element.hovercard-has-image {
  width: 400px;
}

.hovercard-arrow {
  position: absolute;
  z-index: 16;
  display: none;
  content: "";
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid whitesmoke;
}

.hovercard-element.hovercard-visible,
.hovercard-arrow.hovercard-visible {
  display: block;
}

.hovercard-element .hovercard-title {
  font-size: inherit;
  margin-top: 0;
  margin-bottom: 1rem;
}
.hovercard-element .hovercard-description {
  margin-bottom: 0;
}
.hovercard-element.hovercard-has-image .hovercard-description {
  max-width: 250px;
}
.hovercard-element .hovercard-description::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 175px;
  height: 55px;
  background: linear-gradient(transparent, whitesmoke);
}
.hovercard-element .hovercard-image {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 150px;
  background-size: cover;
  background-position: center;
}
